
'use server';
/**
 * @fileOverview A simple chatbot flow that responds to user queries.
 * 
 * - chat - A function that handles the chat conversation.
 */

import { ai } from '@/ai/genkit';
import type { ChatInput, ChatOutput } from "@/lib/types";
import { ChatInputSchema, ChatOutputSchema } from '@/lib/types';


export async function chat(input: ChatInput): Promise<ChatOutput> {
  return await chatFlow(input);
}

const chatPrompt = ai.definePrompt(
  {
    name: 'chatPrompt',
    input: { schema: ChatInputSchema },
    output: { schema: ChatOutputSchema },
    prompt: `You are BharatAI Bazaar, an AI marketplace copilot for India. Your job is to help buyers find the best deals and sellers grow smarter. Be concise, professional, and helpful.

    Current Conversation:
    {{#each history}}
      {{#if (eq role 'user')}}
        User: {{content}}
      {{/if}}
      {{#if (eq role 'bot')}}
        Assistant: {{content}}
      {{/if}}
    {{/each}}

    User: {{{message}}}
    Assistant:`,
  },
);

const chatFlow = ai.defineFlow(
  {
    name: 'chatFlow',
    inputSchema: ChatInputSchema,
    outputSchema: ChatOutputSchema,
  },
  async (input) => {
    const { output } = await chatPrompt(input);
    return output!;
  }
);
