'use server';

/**
 * @fileOverview Flow for calculating profit margins by comparing wholesale and online retail prices.
 *
 * - calculateProfitMargin - Function to calculate profit margins.
 * - CalculateProfitMarginInput - Input type for the calculateProfitMargin function.
 * - CalculateProfitMarginOutput - Output type for the calculateProfitMargin function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const CalculateProfitMarginInputSchema = z.object({
  wholesalePrice: z.number().describe('The price of the product at wholesale.'),
  retailPrice: z.number().describe('The price of the product at online retail.'),
  productName: z.string().describe('The name of the product.'),
  wholesaleSource: z.string().describe('The source of the wholesale price (e.g., shop name or receipt ID).'),
  retailSource: z.string().describe('The online retailer (e.g., Amazon, Flipkart).'),
});

export type CalculateProfitMarginInput = z.infer<typeof CalculateProfitMarginInputSchema>;

const CalculateProfitMarginOutputSchema = z.object({
  profitMargin: z.number().describe('The calculated profit margin as a percentage.'),
  profitAmount: z.number().describe('The calculated profit amount.'),
  isProfitable: z.boolean().describe('Whether the product is profitable based on the given prices.'),
  analysis: z.string().describe('An analysis of the profit margin, including potential risks and opportunities.'),
});

export type CalculateProfitMarginOutput = z.infer<typeof CalculateProfitMarginOutputSchema>;

export async function calculateProfitMargin(input: CalculateProfitMarginInput): Promise<CalculateProfitMarginOutput> {
  return calculateProfitMarginFlow(input);
}

const calculateProfitMarginPrompt = ai.definePrompt({
  name: 'calculateProfitMarginPrompt',
  input: {schema: CalculateProfitMarginInputSchema},
  output: {schema: CalculateProfitMarginOutputSchema},
  prompt: `You are an expert in financial analysis, specializing in profit margin calculation for e-commerce and wholesale businesses.

  Given the following information, calculate the profit margin, profit amount, and determine if the product is profitable. Also, provide a brief analysis of the profit margin, including potential risks and opportunities.

  Product Name: {{{productName}}}
  Wholesale Price: {{{wholesalePrice}}}
  Retail Price: {{{retailPrice}}}
  Wholesale Source: {{{wholesaleSource}}}
  Retail Source: {{{retailSource}}}

  Consider factors such as market trends, competition, and potential costs (e.g., shipping, fees) in your analysis.

  Ensure that the profitMargin is a percentage value, and isProfitable is a boolean value based on whether the profitMargin is positive.
  `,
});

const calculateProfitMarginFlow = ai.defineFlow(
  {
    name: 'calculateProfitMarginFlow',
    inputSchema: CalculateProfitMarginInputSchema,
    outputSchema: CalculateProfitMarginOutputSchema,
  },
  async input => {
    const {output} = await calculateProfitMarginPrompt(input);
    return output!;
  }
);
