
import type { CalculateProfitMarginInput as CaclulateInput, CalculateProfitMarginOutput } from '@/ai/flows/calculate-profit-margin';
import { z } from 'zod';


export type Calculation = CaclulateInput & CalculateProfitMarginOutput & {
  id: string;
  createdAt: string;
};


export const profitMarginInputSchema = z.object({
  productName: z.string().min(1, "Product name is required."),
  wholesalePrice: z.coerce.number({invalid_type_error: "Must be a number"}).min(0, "Wholesale price must be a positive number."),
  retailPrice: z.coerce.number({invalid_type_error: "Must be a number"}).min(0, "Retail price must be a positive number."),
  wholesaleSource: z.string().min(1, "Wholesale source is required."),
  retailSource: z.string().min(1, "Retail source is required."),
});

export type CalculateProfitMarginInput = z.infer<typeof profitMarginInputSchema>;
export type ProfitMarginServerInput = z.input<typeof profitMarginInputSchema>;


const HistoryMessageSchema = z.object({
  role: z.enum(['user', 'bot']),
  content: z.string(),
});

export const ChatInputSchema = z.object({
  history: z.array(HistoryMessageSchema).describe('The conversation history.'),
  message: z.string().describe('The latest user message.'),
});

export type ChatInput = z.infer<typeof ChatInputSchema>;
export type ChatServerInput = z.input<typeof ChatInputSchema>

export const ChatOutputSchema = z.string().describe('The chatbot response.');
export type ChatOutput = z.infer<typeof ChatOutputSchema>;
