
"use server";

import { calculateProfitMargin } from "@/ai/flows/calculate-profit-margin";
import type { CalculateProfitMarginInput, ProfitMarginServerInput } from "@/lib/types";
import { chat } from "@/ai/flows/chat";
import type { ChatInput, ChatServerInput } from "@/lib/types";

export async function getProfitMargin(values: CalculateProfitMarginInput) {

  try {
    const result = await calculateProfitMargin(values);
    return { success: true, data: result };
  } catch (error) {
    console.error("AI flow failed:", error);
    return { error: "Failed to calculate profit margin. Please try again." };
  }
}

export async function getChatResponse(history: ChatInput['history'], message: string) {
    try {
        const response = await chat({ history, message });
        return { response };
    } catch (error) {
        console.error("Chat flow failed:", error);
        return { error: "Failed to get response from the assistant." };
    }
}
